import React from 'react';

import Modal from './Modal';
import  {Link} from 'react-router-dom';
import history from '../history';

class GameOver extends React.Component {

     renderActions(){
         return(
        <React.Fragment> 
            <button onClick={() => window.location.href = "https://simpchess.com/chessapp"} className="ui teal inverted fluid button">Main Menu</button>
            <Link to = {this.props.url} className="ui violet inverted fluid button">Return</Link>
        </React.Fragment>
         );
     };

     renderContent(){
        return this.props.msg;     
     };

    render(){
        return (
            <div>
                <Modal header="Game Over" content={this.renderContent()} onDismiss={() => history.push(this.props.url)} actions = {this.renderActions()}/>
            </div>
    );
    }

};

export default GameOver;
