let check = true;
let canMove = false;
let enPassante = {};

const chessBoard = [
    [{},{},{},{},{},{},{},{}],
    [{},{},{},{},{},{},{},{}],
    [{},{},{},{},{},{},{},{}],
    [{},{},{},{},{},{},{},{}],
    [{},{},{},{},{},{},{},{}],
    [{},{},{},{},{},{},{},{}],
    [{},{},{},{},{},{},{},{}],  
    [{},{},{},{},{},{},{},{}],                  
                      ];

let undoArray = [];

const undoMove = () =>{
   if (!undoArray[0]) {return false;}
   resetBoard(true);
   
   
   let item = undoArray[undoArray.length-1];

   if (item.castle){
       if (item.castle === "Wrook1"){
            Wking.posX = 4;
            Wking.initialpos = true;
            Wrook1.posX = 0;
            Wrook1.initialpos = true;
            chessBoard[7][3] = {};
            chessBoard[7][2] = {};
            chessBoard[7][4] = Wking;
            chessBoard[7][0] = Wrook1;
       }
       else if (item.castle === "Wrook2"){
        Wking.posX = 4;
        Wking.initialpos = true;
        Wrook2.posX = 7;
        Wrook2.initialpos = true;
        chessBoard[7][5] = {};
        chessBoard[7][6] = {};
        chessBoard[7][4] = Wking;
        chessBoard[7][7] = Wrook2;    
       }
       else if (item.castle === "Brook1"){
        Bking.posX = 4;
        Bking.initialpos = true;
        Brook1.posX = 0;
        Brook1.initialpos = true;
        chessBoard[0][3] = {};
        chessBoard[0][2] = {};
        chessBoard[0][4] = Bking;
        chessBoard[0][0] = Brook1;    
        }
        else if (item.castle === "Brook2"){
            Bking.posX = 4;
            Bking.initialpos = true;
            Brook2.posX = 7;
            Brook2.initialpos = true;
            chessBoard[0][5] = {};
            chessBoard[0][6] = {};
            chessBoard[0][4] = Bking;
            chessBoard[0][7] = Brook2; 
            
        }
   }

   else {for (let i in allpieces){
        if (allpieces[i].posX === item.newpiece.posX && allpieces[i].posY === item.newpiece.posY && allpieces[i].special === item.oldpiece.special){
            allpieces[i].posX = item.oldpiece.posX;
            allpieces[i].posY = item.oldpiece.posY;
            allpieces[i].img = item.oldpiece.img;
            allpieces.type = item.oldpiece.type;
            allpieces[i].possibleMoves = item.oldpiece.possibleMoves;
            chessBoard[item.oldpiece.posY][item.oldpiece.posX] = allpieces[i];
            break;
        }
   }}

   if (item.newpiece && item.newpiece.team){
        for (let i in allpieces){
            if (allpieces[i].posX === item.newpiece.posX && allpieces[i].posY === item.newpiece.posY && allpieces[i].special === item.newpiece.special){
                
                chessBoard[item.newpiece.posY][item.newpiece.posX] = allpieces[i];
                break;
            }
        }
    } else if (item.newpiece) {chessBoard[item.newpiece.posY][item.newpiece.posX] = {};}


   
   if (item.enPassante !== true){
       chessBoard[item.enPassante.posY][item.enPassante.posX].enpassante = item.enPassante.enPassante;
       enPassante = {posX: item.enPassante.posX, posY: item.enPassante.posY};
   }
   
   let newitem = undoArray[undoArray.length-1];

   if (newitem.enPassante && newitem.enPassante.posX && newitem.enPassante.posX === item.newpiece.posX && newitem.enPassante.posY === item.newpiece.posY && item.oldpiece.type === "pawn"){
    
        for (let i in allpieces){
            if (allpieces[i].posX === newitem.enPassante.enPassante.x && allpieces[i].posY === newitem.enPassante.enPassante.y){
                
                chessBoard[newitem.enPassante.enPassante.y][newitem.enPassante.enPassante.x] = allpieces[i];
                break;
            }
        }
    
   }
  
   undoArray.pop();
   return true;
   
}

const resetBoard = (enpass) => {
    if (check ===false){return;}
    for (let row in chessBoard){
        for (let column in chessBoard[row]){
            chessBoard[row][column].movePiece = '';
            chessBoard[row][column].availability = '';
            chessBoard[row][column].selected = false;
             
            if (enpass && chessBoard[row][column].enpassante){
            
                chessBoard[row][column].enpassante = '';
                enPassante = {};
            
            }
            
        }
    }
}

const checkForMate = (turn) =>{
    canMove = false;

    for (let row in chessBoard){
        for (let column in chessBoard[row]){
            
            if (chessBoard[row][column].possibleMoves && chessBoard[row][column].team === turn){       
                chessBoard[row][column].possibleMoves();
               
                resetBoard();
                
                if (canMove === true){
                    return false;
                }

            }
        }
    }
    if ( (turn === "white" && checkforcheck(Wking, Wking.posX, Wking.posY) === false) || (turn === "black" && checkforcheck(Bking, Bking.posX, Bking.posY) === false) ){check = true; return "checkmate";}
    else {return "stalemate";}
}

const checkAlgorithm = (team) =>{
    for (let row in chessBoard){
        for (let column in chessBoard[row]){
            if (chessBoard[row][column].possibleMoves && chessBoard[row][column].team === team){
                chessBoard[row][column].possibleMoves();
            }
        }
    }
}   

const resetChecks = () =>{
    for (let row in chessBoard){
        for (let column in chessBoard[row]){
            chessBoard[row][column].check = false;
        }
    }
}

const checkforcheck = (piece, newX, newY) =>{
    resetChecks();
    check = false;
    
    let oldteam1 = chessBoard[piece.posY][piece.posX].team;
    let oldteam2 = chessBoard[newY][newX].team;
    chessBoard[newY][newX].team = piece.team;
    chessBoard[piece.posY][piece.posX].team = '';
    if (piece.type === 'pawn' && chessBoard[newY][newX].enpassante){
        let enpassteam = chessBoard[chessBoard[newY][newX].enpassante.y][chessBoard[newY][newX].enpassante.x].team;
        chessBoard[chessBoard[newY][newX].enpassante.y][chessBoard[newY][newX].enpassante.x].team = '';
        var callback = () => chessBoard[chessBoard[newY][newX].enpassante.y][chessBoard[newY][newX].enpassante.x].team = enpassteam;
    }
    else { callback = undefined;}

    if (piece.type === 'king'){
        var oldposx = piece.posX;
        var oldposy = piece.posY;

        piece.posX = newX;
        piece.posY = newY;
        
    }

    if (oldteam1 === "white"){
        checkAlgorithm("black");
    
        if (chessBoard[Wking.posY][Wking.posX].check){
            if (piece.type === 'king'){
                piece.posX = oldposx;
                piece.posY = oldposy;
            }
            chessBoard[piece.posY][piece.posX].team = oldteam1;
            chessBoard[newY][newX].team = oldteam2;
            if (callback){callback();}

            resetChecks();
            return false;
        }
    }

    if (oldteam1 === "black"){
        checkAlgorithm("white");
        if (chessBoard[Bking.posY][Bking.posX].check){
            if (piece.type === 'king'){
                piece.posX = oldposx;
                piece.posY = oldposy;
            }
            chessBoard[piece.posY][piece.posX].team = oldteam1;
            chessBoard[newY][newX].team = oldteam2;
            if (callback){callback();}
            
            resetChecks();
            return false;
        }
    }

    if (piece.type === 'king'){
        piece.posX = oldposx;
        piece.posY = oldposy;
    }

    chessBoard[piece.posY][piece.posX].team = oldteam1;
    chessBoard[newY][newX].team = oldteam2;
    if (callback){callback();}
    resetChecks();
    return true;
}

const evolvePawn = (piece) =>{
    piece.type = 'queen';
    piece.possibleMoves =  () => {bishopMoves(piece); rookMoves(piece, true);};
    if (piece.team === 'black'){
        piece.img = bQueenImg;
    }
    else if (piece.team === 'white'){
        piece.img = wQueenImg;
    }
}

const movePiece = (piece, newX, newY, set) => { //castling does not use this function
    //--------------------------------------------------------------------------------moving to en passante square
    if (piece.type === 'pawn' && chessBoard[newY][newX].enpassante){
        chessBoard[chessBoard[newY][newX].enpassante.y][chessBoard[newY][newX].enpassante.x] = {};        
    }
    //--------------------------------------------------------------------------------moving to en passante square

    if (chessBoard[newY][newX].team){var newpiece = chessBoard[newY][newX];} else {newpiece = {posX: newX, posY: newY};}
   
    
    undoArray.push( {oldpiece: {...piece}, newpiece: {...newpiece}, enPassante: (!enPassante.posY || {posX: enPassante.posX, posY: enPassante.posY, enPassante: chessBoard[enPassante.posY][enPassante.posX].enpassante } ) } );
    


    //deletes possible move click events and deletes old piece
    resetBoard(true); //deletes en passante values on the board
    chessBoard[piece.posY][piece.posX] = {};
    //deletes possible move click events and deletes old piece

    //---------------------------------------------------------------moving via en passante -> sets en passante value to jumped square
    if (piece.type === 'pawn' && (piece.posY === 1 || piece.posY === 6) && (newY === 3 || newY === 4) ){
        chessBoard[(piece.posY+newY)/2][newX].enpassante = {x: newX, y: newY};
        enPassante = {posX: newX, posY: (piece.posY+newY)/2}
    }
    //---------------------------------------------------------------moving via en passante -> sets en passante value to jumped square

    //sets new pos values
    piece.posX = newX;
    piece.posY = newY;
    //sets new pos values

    //evolves pawn when reaching the end 
    if (piece.type === 'pawn' && (piece.posY === 0 || piece.posY === 7)){
        evolvePawn(piece);
    }
    //evolves pawn when reaching the end 

    //prevents castling after moving a castle piece
    if (!set && piece.initialpos && piece.initialpos === true){piece.initialpos = false;}
    //prevents castling after moving a castle piece

    //set new piece
    chessBoard[piece.posY][piece.posX] = piece; 
    //set new piece
    
    
};

const possibleMoves = (piece, x,y, enpassenemy, falsePawn) =>{
    
    if (!inTheBoard(x, y)){return false;}

    if (check === false){
        if (!falsePawn){
            chessBoard[y][x].check = true;
        }
        if (chessBoard[y][x].team){return false;}
        else {return true;}
    }

    
    if (check === true){
        
        if (checkforcheck(piece, x, y) === false){check = true; if (chessBoard[y][x].team){return false;} else{return true;}}
        else {check = true;}
    }
    


    if (!chessBoard[y][x].team ){
        chessBoard[y][x] = {...chessBoard[y][x], availability: (enpassenemy || "open"), movePiece: () => movePiece(piece,x,y)};
        canMove = true;
        
        return true;
    }
    
    else if (chessBoard[y][x].team !== piece.team){
        chessBoard[y][x] = {...chessBoard[y][x], availability: "enemy", movePiece: () => movePiece(piece,x,y)};

        canMove = true;
        return false;
    }

    else { return false;}

    
}

const inTheBoard = (newX, newY) =>{
    return (-1 <newX && newX<8 && -1<newY && newY<8); //checks if new pos is in the board
}

class Piece {
    constructor(x,y, type, team, img, moves, special){
        this.posX = x;
        this.posY = y;
        this.type = type;
        this.team = team;
        this.img = img;
        this.possibleMoves = moves;
        this.special = special;

        if (type === 'king' || type === 'rook'){
            this.initialpos = true;
        }
              
    }

}

