const kingMoves = (king) =>{
    resetBoard();

    let newX = king.posX;
    let newY = king.posY + 1;
    //if (!check){addmoves}
    possibleMoves(king, newX, newY);

    newX = king.posX +1;
    //if (!check){addmoves}
    possibleMoves(king, newX, newY);

    newX = king.posX -1;
    //if (!check){addmoves}
    possibleMoves(king, newX, newY);


    newX = king.posX;
    newY = king.posY -1;
    //if (!check){addmoves}
    possibleMoves(king, newX, newY);

    newX = king.posX +1;
    //if (!check){addmoves}
    possibleMoves(king, newX, newY);

    newX = king.posX -1;
    //if (!check){addmoves}
    possibleMoves(king, newX, newY);


    newX = king.posX -1;
    newY = king.posY;
    //if (!check){addmoves}
    possibleMoves(king, newX, newY);

    newX = king.posX +1;
    //if (!check){addmoves}
    possibleMoves(king, newX, newY);
    if (king.initialpos){castle(king.team);}
};


