import React from 'react';
import {connect} from 'react-redux';


import chessBoard from './Pieces';
import {resetBoard, checkForMate, undoMove} from './Pieces';
import {setBoard} from '../actions';
import history from '../history';
import '../responsive.css';

let color = "brown";
let turn = "white";
let gameovermsg = "";

class Game extends React.Component{
    constructor(props){
        super(props);
        this.props.setBoard(chessBoard);
    }

    changeTurns = () =>{
        if (turn === "white"){
            turn = "black";
            if (checkForMate("black") === "checkmate"){
                gameovermsg = "Checkmate By White"
                history.push("/gameover");
            }
            else if (checkForMate("black") === "stalemate"){
                gameovermsg = "Stalemate"
                history.push("/gameover");
            }
        }
        else{
            turn = "white";
            if (checkForMate("white") === "checkmate"){
                gameovermsg = "Checkmate By Black"
                history.push("/gameover");
            }
            else if (checkForMate("white") === "stalemate"){
                gameovermsg = "Stalemate"
                history.push("/gameover");
            }
        }
    }

    handleClick = (piece) =>{
        if (piece.selected === true){
            resetBoard();
            piece.selected = false;
            this.props.setBoard(chessBoard);
        }

        else if (piece.availability === "open" || piece.availability === "enemy" || piece.availability === "enpassenemy"){
            piece.movePiece();
            piece.selected = false;
            this.props.setBoard(chessBoard);
            this.changeTurns();
            //socket.emit here
        }

        else if (piece.possibleMoves && (piece.team === turn) ){
           
            piece.possibleMoves()
            piece.selected = true;
            this.props.setBoard(chessBoard);
        }
        else{
            resetBoard();
            this.props.setBoard(chessBoard);
        }
    }

    renderCircle(piece){
        if (piece.availability && piece.availability === "open"){
            return <i id = "rCircle1" className = "green huge icon circle outline"></i>;
        }
        else if (piece.availability && piece.availability === "enemy"){
            return <i id = "rCircle2" className = "red huge icon circle outline"></i>;
        }
        else if (piece.availability && piece.availability === "enpassenemy"){
            return <i id = "rCircle3" className = "red huge icon circle outline"></i>;
        }
    }

    renderBoard(){
        
        return this.props.board.map((row, rowindex) =>{
            return row.map((piece, columnindex) =>{
                
                if ( (rowindex+columnindex) %2 === 0){
                    color = "#ffe8c7";
                    if (piece.selected){
                        color = "#7DAE6C";
                    }
                }

                else {
                    color = "#b3834d";
                    if (piece.selected){
                        color = "#556E3C";
                    }
                }

                

                if (piece.team){
                    return <div key = {(8*rowindex)+(columnindex+1)} onClick = { () => this.handleClick(piece)} style={{ backgroundColor: `${color}`}} ><img style = {{width: "100%", height: "auto"}} src= {piece.img} alt = {piece.img}></img>{this.renderCircle(piece)}</div>;
                }
                else {
                    return <div key = {(8*rowindex)+(columnindex+1)} onClick = { () => this.handleClick(piece)} style={{ backgroundColor: `${color}`}} >{this.renderCircle(piece)}</div>; 
                }

            })
        })       
    }


    buttonStyle = {alignSelf: "center", position: "relative", whiteSpace: "nowrap", paddingLeft: "15vw", paddingRight: "15vw", marginRight: "0vw", fontSize: "2vw"};

    render(){
            return (
                <div  style = {{display: "flex", paddingTop: "10vh"}}>
                    <button onClick={() => window.location.href = "https://simpchess.com/chessapp"}  id="rButton1" className = "ui button pink inverted">MAIN MENU</button>
                    <div className = "rGrid ui raised segments" >{this.renderBoard()}</div>
                    <button onClick = { () => {
                        if (undoMove()){    //socket.emit here
                            if (turn ==="white") {
                                turn ="black";
                            }
                            else{
                                turn="white";
                            }
                        this.props.setBoard(chessBoard);}}} id="rButton2" className = "ui button pink inverted">UNDO MOVE</button>
                </div>
                            
                );
    }

}

const mapStateToProps = (state) => {
    return {
            board: state.boardReducer
    };
};

export default connect(mapStateToProps, {setBoard})(Game);

export {gameovermsg};