import React from 'react';
import ReactDOM from 'react-dom';

const Modal = props =>{
    return ReactDOM.createPortal(
        <div style = {{position: "fixed", top: "30%", left: "50%", height: "100vh", width: "100vw", paddingBottom: "10vh"}} onClick = {props.onDismiss} className = "ui dimmer basic modals visible active">
            <div onClick={(e) => e.stopPropagation()} className = "ui basic modal visible active">
                <div style = {{color: "#d6feff"}} className = "ui header center aligned">
                    {props.header}
                    <br/>    
                    <br/>
                    <br/>           
                    {props.content}
                    <br/>
                    <br/>
                    {props.actions}
                </div>
            </div>
        </div>,
        document.querySelector('#modal')
    );
};

export default Modal;